/* Navbar Container */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #d63384;
  padding: 15px 30px;
  color: white;
  font-size: 1.1rem;
}

/* Logo */
.logo a {
  text-decoration: none;
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
}

/* Navigation Links */
.navbar nav {
  display: flex;
  gap: 15px;
}

.navbar nav a {
  text-decoration: none;
  color: white;
  padding: 8px 12px;
  border-radius: 5px;
  transition: background 0.3s ease-in-out;
}

.navbar nav a:hover {
  background: rgba(255, 255, 255, 0.2);
}

/* Dropdown Container */
.dropdown {
  position: relative;
  display: inline-block;
}

/* Dropdown Button */
.dropbtn {
  background: transparent;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 1.1rem;
  padding: 8px 12px;
}

/* Dropdown Content */
.dropdown-content {
  display: none;
  position: absolute;
  background: #000000;
  min-width: 140px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  z-index: 1;
  overflow: hidden;
}

/* Dropdown Links */
.dropdown-content a {
  color: #333;  /* Changed from white to dark for visibility */
  padding: 10px;
  text-decoration: none;
  display: block;
  transition: background 0.3s ease-in-out;
}

.dropdown-content a:hover {
  background: #d63384;
  color: white;
}

/* Show Dropdown on Hover */
.dropdown:hover .dropdown-content {
  display: block;
}

/* Logout Button */
.logout-btn {
  background: #dc3545;
  color: white;
  padding: 8px 12px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  transition: background 0.3s ease-in-out;
}

.logout-btn:hover {
  background: #c82333;
}

/* Responsive Navbar */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    gap: 10px;
    text-align: center;
  }

  .navbar nav {
    flex-direction: column;
    gap: 10px;
  }

  .dropdown-content {
    position: static;
    width: 100%;
    text-align: center;
  }

  .dropdown-content a {
    display: block;
    width: 100%;
  }
}
